.slider {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 207px;

  @include on-tablet {
    height: 289px;
  }

  @include on-laptop {
    height: 341px;
  }

  @include on-desktop {
    height: 353px;
  }

  &--header {
    height: 100%;
  }

  &__slides {
    width: 100%;
    height: 189px;
    display: flex;
    flex-wrap: nowrap;
    gap: 0;
    overflow-x: auto;
    scroll-behavior: smooth;
    scroll-snap-type: x mandatory;
    scrollbar-color: transparent transparent;
    scrollbar-width: none;

    @include on-tablet {
      height: 270px;
    }

    @include on-laptop {
      overflow: hidden;
      height: 290px;
    }

    @include on-desktop {
      height: 307px;
    }

    &--header {
      height: 100%;
    }
  }

  &__slide {
    flex-shrink: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    scroll-snap-align: start;
    background-image: url('../images/slider/slider-1.png');
    background-color: $backgroundBlackColor;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    &--2 {
      background-image: url('../images/slider/slider-2.png');
    }

    &--3 {
      background-image: url('../images/slider/slider-3.png');
      background-size: contain;
    }

    &--4 {
      background-image: url('../images/slider/slider-4.png');
      background-size: contain;
    }

    &--5 {
      background-image: url('../images/slider/slider-5.png');
      background-size: contain;
    }

    &--header-bg {
      background-color: transparent;
      background-image: url('../images/main/header-image.svg');
      background-size: contain;
    }
  }

  &__points {
    display: flex;
    gap: 6px;

    @include on-laptop {
      display: none;
    }
  }

  &__point {
    width: 4px;
    height: 4px;

    background-image: url('../images/slider/slider-dot.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    &--active {
      background-image: url('../images/slider/slider-dot-active.svg');
    }
  }

  &__prev-next {
    display: none;

    @include on-laptop {
      display: block;
      align-self: flex-start;
    }
  }
}
