.footer {
  &__content {
    @include content-min-width;
    @include grid-container;

    box-sizing: border-box;
    padding: 45px 0 50px;
    background-color: $blackColor;

    @include on-tablet {
      align-items: center;
      padding-block: 21px;
    }

    @include on-laptop {
      padding-block: 39px;
    }
  }

  &__logo {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    margin-bottom: 15px;

    @include on-tablet {
      margin-bottom: 0;
      justify-self: start;
    }

    @include on-laptop {
      grid-column: 1 / 3;
    }
  }

  &__nav {
    grid-column: 1 / 2;
    grid-row: 2 / 4;

    @include on-tablet {
      grid-column: 2 / -2;
      grid-row: 1 / 2;
      justify-self: center;
    }

    @include on-laptop {
      grid-column: 3 / -3;
    }
  }

  &__contacts {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    align-self: start;

    @include on-tablet {
      display: none;
    }
  }

  &__socials {
    grid-column: 2 / 3;
    grid-row: 3 / 4;
    align-self: end;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include on-tablet {
      grid-column: -2 / -1;
      grid-row: 1 / 2;
      align-self: center;
    }

    @include on-laptop {
      grid-column: -3 / -1;
      justify-self: end;
      justify-content: flex-end;
      gap: 18px;
    }
  }

  &__social {
    width: 7.57px;
    height: 15.15px;
    background-image: url('../images/icons/facebook.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @include on-tablet {
      width: 6px;
      height: 12px;
    }

    @include pointer-cursor;
    @include on-hover(transform, scale(1.3));

    &--x {
      width: 18.93px;
      height: 13.88px;
      background-image: url('../images/icons/x.svg');

      @include on-tablet {
        width: 15px;
        height: 11px;
      }
    }

    &--youtube {
      width: 17.9px;
      height: 13.63px;
      background-image: url('../images/icons/youtube.svg');

      @include on-tablet {
        width: 14.18px;
        height: 10.8px;
      }
    }

    &--discord {
      width: 17.67px;
      height: 17.67px;
      background-image: url('../images/icons/discord.svg');

      @include on-tablet {
        width: 14px;
        height: 14px;
      }
    }
  }
}
